<template>
  <div>
    <h1>Меню ресторана: {{ cafeId }}</h1>
    <h1>Номер выбранной категории: {{ categoryID }}</h1>
    <h1>{{ infoId }}</h1>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      id: 0,
      loading: false,
      post: null,
      error: null,
      item: {},
      cafeId: 0,
      categoryID: 0,
    }
  },
  created() {
    this.cafeId = this.$route.params.id_restarant
    this.categoryID = this.$route.params.id_category
    this.fetchData()
  },

  methods: {
    fetchData() {
      this.loading = true
      const url = `https://605b03b727f0050017c05e33.mockapi.io/restarants/${this.id}/menu`
      axios
        .get(url)
        .then((r) => {
          this.loading = false
          if (r.status === 200) {
            this.item = r.data
          }
        })
        .catch((e) => {
          console.log(e)
          this.loading = false
        })
    },
  },
}
</script>
