<template>
  <div>
    <div v-if="!loading">
      <h1>Ресторан {{ item.name }}</h1>
      <h3>Адрес ресторана: {{ item.address }}</h3>
      <h3>Телефон для бронирования: {{ item.phone }}</h3>

      <h2>category</h2>
      <ul>
        <li v-for="(category, i) in categories" :key="i">
          <router-link
            :to="{
              name: 'MenuRestaurant',
              params: {
                id_restarant: id,
                id_category: category.id,
              },
            }"
            ><img :src="category.img" />
            {{ category.name }}
          </router-link>
        </li>
      </ul>
    </div>
    <div v-else></div>
  </div>
</template>

<script>
import server from '../service/api'

export default {
  data() {
    return {
      id: 0,
      loading: false,
      post: null,
      error: null,
      categories: [],
    }
  },
  async created() {
    this.id = this.$route.params.id
    this.loading = true
    this.item = await server.loadRestaurantInfo(this.id)
    this.categories = await server.loadCategories(this.id)

    if (!this.item) {
      alert('error')
    }
    this.loading = false
  },

  methods: {},
}
</script>
