<template>
  <div class="home">
    <ul class="mainMenu">
      <li v-for="(item, i) in allRestaraunts" :key="i">
        <router-link
          :to="{
            name: 'Restarant',
            params: {
              id: item.id,
            },
          }"
        >
          <div class="itemMainMenu">{{ item.name }}</div>
          <!-- {{ item.address }} -->
          <img class="itemImg" :src="item.logo" />
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'
import 'ant-design-vue/dist/antd.css'
import Vue from 'vue'
import { DatePicker } from 'ant-design-vue'
Vue.use(DatePicker)

export default {
  name: 'Home',
  data() {
    return {
      allRestaraunts: [],
      id: '',
    }
  },
  created() {
    axios
      .get('https://605b03b727f0050017c05e33.mockapi.io/restarants/')
      .then((responce) => {
        if (responce.status === 200) {
          this.allRestaraunts = responce.data
        }
      })
  },
}
</script>

