import axios from 'axios'

const baseURL = `https://605b03b727f0050017c05e33.mockapi.io`
const api = axios.create({ baseURL })

function loadRestaurantInfo(id) {
  return new Promise((resolve, reject) => {
    api
      .get(`/restarants/${id}`)
      .then((r) => {
        if (r.status === 200) {
          resolve(r.data)
        }

        reject()
      })
      .catch((e) => {
        console.log(e)
        reject()
      })
  })
}

function loadCategories(id) {
  console.log(id)
  return new Promise((resolve, reject) => {
    api
      .get(`/categories`)
      .then((r) => {
        if (r.status === 200) {
          resolve(r.data)
        }

        reject()
      })
      .catch((e) => {
        console.log(e)
        reject()
      })
  })
}

const server = {
  loadRestaurantInfo,
  loadCategories,
}
export default server
